body {
    transition: background-color 1.0s, color 1.0s;
}

#toggle-btn>img {
    width: 30px;
    height: 30px;
}


#toggle-btn {
    position: fixed;
    top: 20px;
    right: 20px;
    cursor: pointer;
    background: none; 
    border: none;
    z-index: 10;
}


body.light-mode {
    background-color: white;
    color: black;
}

body.dark-mode {
    background-color: black;
    color: white;
}

#stars {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.star,
.asteroid {
    position: absolute;
    background-color: #000000;
    border-radius: 50%;
    animation: twinkle 1s infinite alternate, move 10s infinite linear;
}

@keyframes twinkle {
    from {
        opacity: 0.8;
    }
    to {
        opacity: 1;
    }
}

@keyframes move {
    0% {
        transform: translate(0, 0);
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        transform: translate(calc(100vw - 100%), -100vh);
        opacity: 0;
    }
}
